<template>
  <div class="px-3 py-2">
    <div class="d-flex justify-content-between align-items-center">
      <div class="d-flex align-items-center">
        <img src="@/assets/images/logo/logo.png" alt="logo" width="80" />
        <span class="title-logo"> Smartwork </span>
      </div>

      <b-link
        to="/performance"
        class="d-flex align-items-center bg-white p-1 rounded-pill cursor-pointer"
      >
        <feather-icon icon="ArrowLeftIcon" />
        <span class="bold ml-1">Close</span>
      </b-link>
    </div>

    <div class="text-center mt-3">
      <h2 style="color: #1e1e1e">Summary</h2>
      <span class="text-muted">
        Please review your goals before submitting
      </span>
    </div>

    <b-row class="justify-content-center">
      <b-col md="8">
        <b-card class="mt-3 rounded-3">
          <b-card
            v-for="(data, index) in strategy"
            :key="data"
            :title="'# Goals ' + (index + 1)"
          >
            <b-row>
              <b-col md="6">
                <b-form-group label="Strategic Target">
                  <b-form-input
                    v-model="strategy[index]"
                    placeholder="Strategic Target"
                    required
                    class="rounded-pill bg-white"
                    disabled
                  ></b-form-input>
                </b-form-group>
                <b-form-group label="Weight">
                  <b-form-input
                    v-model="weight[index]"
                    placeholder="Weight"
                    required
                    class="rounded-pill bg-white"
                    disabled
                  ></b-form-input>
                </b-form-group>
                <b-form-group label="Target">
                  <b-form-input
                    v-model="target[index]"
                    placeholder="Target"
                    required
                    class="rounded-pill bg-white"
                    disabled
                  ></b-form-input>
                </b-form-group>
                <b-form-group label="Due Date">
                  <flat-pickr
                    v-model="due_date[index]"
                    placeholder="Due Date"
                    class="rounded-pill bg-white"
                    :config="{
                      altInput: true,
                      altFormat: 'F j, Y',
                      dateFormat: 'Y-m-d',
                    }"
                    disabled
                  ></flat-pickr>
                </b-form-group>
              </b-col>
              <b-col md="6">
                <b-form-group label="KPI">
                  <b-form-textarea
                    v-model="kpi[index]"
                    placeholder="KPI"
                    required
                    class="bg-white"
                    disabled
                    style="min-height: 277px; max-height: 277px"
                  ></b-form-textarea>
                </b-form-group>
              </b-col>
            </b-row>
          </b-card>
          <div class="text-right">
            <b-button
              type="submit"
              variant="primary"
              class="rounded-3"
              @click="submit_goals($event)"
            >
              Submit
            </b-button>
          </div>
        </b-card>
      </b-col>
    </b-row>
  </div>
</template>

<style>
.title-logo {
  font-size: 1.5rem;
  font-weight: 600;
  color: #423ffd;
  margin-left: 0.5rem;
}

.bg-white {
  background-color: #fff;
}

.rounded-3 {
  border-radius: 1.75rem;
}
</style>

<script>
export default {
  data() {
    return {
      strategy: "",
      weight: "",
      target: "",
      due_date: "",
      kpi: "",
    };
  },
  mounted() {
    // get data from local storage
    let goals = JSON.parse(localStorage.getItem("sw_goals")) || [];
    goals.map((item) => {
      this.strategy = item.strategy;
      this.weight = item.weight;
      this.target = item.target;
      this.due_date = item.due_date;
      this.kpi = item.kpi;
    });
  },
};
</script>